<template>
  <div class="quota2">
    <div class="icon"><img v-if="icon" :src="icon" :style="imageStyle" /></div>
    <div class="quota">
      <div class="data" v-if="data">
        <count-to
          class="digital"
          :startVal="prevData"
          :endVal="data"
          :style="dataStyle"
        />
        <span class="unit" :style="suffixStyle">{{ suffix }}</span>
      </div>
      <div class="title" v-if="title" :style="titleStyle">{{ title }}</div>
    </div>
  </div>
</template>

<script>
import { ref, watch } from "vue";

export default {
  props: {
    icon: {
      type: String,
      default: null,
    },
    imageStyle: {
      type: Object,
      default() {
        return {
          width: "6rem",
        };
      },
    },
    title: {
      type: String,
      default: null,
    },
    titleStyle: {
      type: Object,
      default() {
        return {
          color: "#ffffff",
          fontSize: "1.5rem",
        };
      },
    },
    data: {
      type: Number,
      default: null,
    },
    dataStyle: {
      type: Object,
      default() {
        return {
          color: "#00ffff",
          fontSize: "2rem",
          fontWeight: "bold",
        };
      },
    },
    suffix: {
      type: String,
      default: "",
    },
    suffixStyle: {
      default() {
        return {
          color: "#ffffff",
          fontSize: "1.3rem",
        };
      },
    },
  },
  setup(props) {
    const prevData = ref(0);
    watch(
      () => props.data,
      (nextValue, prevValue) => {
        prevData.value = prevValue;
      }
    );

    return {
      prevData,
    };
  },
};
</script>

<style lang="scss" scoped>
.quota2 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url(../../../assets/img/box5.png);
  background-position: center center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  padding: 1rem 2rem;
  min-width: 13rem;
  min-height: 13rem;
  .icon {
    text-align: center;
    img {
      max-width: 5rem;
      max-height: 5rem;
    }
  }
  .quota {
    display: flex;
    flex-direction: column;
    .data {
      text-align: center;
      margin: 0.5rem 0;
      .digital {
        font-family: LCD;
      }
    }
  }
}
</style>